import React from 'react'
import Img from 'gatsby-image'
import styled from 'styled-components'
import useLogos from '../hooks/use-logos'
import ScrollAnimation from 'react-animate-on-scroll'

const StyledSection = styled.section`
  color: white;
  text-align: center;
`

const Wrapper = styled.div`
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  background: black;
  width: 100%;
  height: auto;
  padding: 0 10rem;

  @media (max-width: 768px) {
    padding: 0 2rem;
  }

  h2 {
    text-align: center;
  }
`

const StyledAnimation = styled(ScrollAnimation)`
  margin-bottom: 0;
  :not(:last-child) {
    padding-right: 2rem;

    @media (max-width: 768px) {
      padding-right: 0;
    }
  }
`

const Logos = () => {
  const logos = useLogos()
  const delay = 150

  return (
    <StyledSection>
      <ScrollAnimation animateIn="fadeInUp">
        <h2>Clients</h2>
      </ScrollAnimation>
      <Wrapper>
        {logos.map((logo, idx) => {
          return (
            <StyledAnimation
              key={logo.logo.node.sharp.fixed.src}
              animateIn="growInUp"
              delay={delay * idx}
            >
              <Img fixed={logo.logo.node.sharp.fixed} backgroundColor="black" />
            </StyledAnimation>
          )
        })}
      </Wrapper>
    </StyledSection>
  )
}

export default Logos
