import React from 'react'
import styled from 'styled-components'
import ScrollAnimation from 'react-animate-on-scroll'

const Wrapper = styled.section`
  background: white;
  color: black;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (max-width: 768px) {
    padding: 0 2rem;
  }

  ul {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    list-style-type: none;
    flex-wrap: wrap;
    padding: 0;
    max-width: 550px;
    margin: 0 auto;

    li {
      text-align: center;
      margin-bottom: 1rem;
    }
  }

  h2 {
    text-align: center;
  }
`

const Services = () => {
  const services = [
    'Shopify Setup',
    'Shopify Theme Customization',
    'Product Merchandising',
    'Email Marketing',
    'Social Advertising',
    'Customer Service Management',
    'Physical Fulfillment',
  ]

  const delay = 100

  return (
    <Wrapper>
      <ScrollAnimation animateIn="fadeInUp">
        <h2>Services</h2>
      </ScrollAnimation>
      <ul>
        {services.map((service, idx) => {
          return (
            <li key={service}>
              <ScrollAnimation animateIn="fadeInUp" delay={delay * idx}>
                {service}
              </ScrollAnimation>
            </li>
          )
        })}
      </ul>
    </Wrapper>
  )
}

export default Services
